<template>
  <div class="stats">
    <h1>Link Destroyer</h1>
    <p>There are currently <span> {{ countLinks }}</span> links</p>
    <p>Do you wanna erase them all?</p>

    <button v-on:click="removeAllLinks">Destroy all</button>
    <p>{{ msg }}</p>
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from 'vuex'

export default {
  name: 'Stats',
  data() {
    return {
      msg: ''
    }
  },
  computed: {
    ...mapGetters([
      'countLinks'
    ]),
  },
  methods: {
    ...mapMutations(['REMOVE_ALL']),
    ...mapActions(['removeAll']),
    removeAllLinks() {
      this.removeAll().then(() => {
        this.msg = 'They have been removed'
      });
    }
  }
}
</script>

